import React, { useState } from 'react'
import {BsPersonPlus} from "react-icons/bs"
import back from '../../Assets/images/back.png'
import './index.css'
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom'
import PersonalInfoAr from './steps/PersonalInfo/index-ar';
import CompanyInfoAr from './steps/CompanyInfo/index-ar';
import corporate from '../../Assets/images/ar_photo/corporate.png';
import Axios from 'axios';
import toast, { Toaster } from "react-hot-toast";
import { useSelector } from "react-redux";

function RegisterCorporateAr() {

  const [formErrors, setFormErrors] = useState({});


  const navigate = useNavigate();
  const [ num , setNum ] = useState("")
  const [page , setPage] = useState(0)
  const [data , setData] = useState({
    username:"",
    email:"",
    password:"",
    confirmPassword:"",
    bornDate:"",
    mobileNumber:"",
    fullName:"",
    whatsNumber:"",
    companyName:"",
    typeCompany:"",
    companyRecord:"",
    companyDate:"",
    currentCity:"",
    currentAddress:"",
    detaileBusiness:"",
    companyEmail:"",
    companyMobile:"",
    actualStart:"",
    phoneNumber:"",
    faxNumber:"",
    number_partners:"",
    file_record:"",
    establContract:"",
  })
  const data_ = [
    {
      key:"user_name",
      value:data.username
  },{
    key:"company_name",
    value:data.companyName
  },{
    key:"company_type",
    value:data.typeCompany
  },{
    key:"company_record_history",
    value:data.companyDate
  },{
    key:"city",
    value:data.currentCity
  },{
    key:"current_address",
    value:data.currentAddress
  },{
    key:"current_company_activity_details",
    value:data.detaileBusiness
  },{
    key:"phone_number",
    value:num
  },{
    key:"work_start_date",
    value:data.actualStart
  },{
    key:"landline_number",
    value:data.phoneNumber
  },{
    key:"fax_number",
    value:data.faxNumber
  },{
    key:"number_of_partners",
    value:data.number_partners
  },{
      key:"company_record_number",
      value:data.companyRecord
  },{
        key:"email",
        value:data.companyEmail
    },{
      key:"password",
      value:data.password
  },{
        key:"file1",
        value:data.file_record
    },{
        key:"file2",
        value:data.establContract
    },
]


  //--------------add validation ----------
  const validate = (values) => {
    let errors = {};
    if(!values.username){
      errors.username="من فضلك ادخل اسم المستخدم"
    }
    if(values.password.length < 4){
      errors.password = "كلمة المرور يجب ان تحوي أكثر من 4 محارف"
    }
    if(values.password !== values.confirmPassword){
      errors.confirmPassword="يجب ان تتطابق كلمات المرور"
    }
    return errors;
  }

  const BASE_API_URL = useSelector(state => state.BASE_API_URL);

const handleSubmit= ()=>{

  if(!data.companyName || !data.number_partners || !data.companyEmail || !data.companyRecord){
    toast.error("من فضلك أدخل كامل بياناتك في الحقول")
  }
  setFormErrors(validate(data))
  const formData = new FormData();
  data_.map((item)=>(
      formData.append(item.key , item.value)
  ))
  Axios
    .post(`${BASE_API_URL}/api/companies/add-new-user`, formData)
    .then((res) => {
      const data1 = res.data;
      if(data1 === "عذراً البريد الالكتروني الذي أدخلته موجود مسبقاً ،  من فضلك أدخل بريد الكتروني آخر ..."){
          toast.error("عذراً البريد الالكتروني الذي أدخلته موجود مسبقاً ،  من فضلك أدخل بريد الكتروني آخر")
      }else if( data1 === "عذراً ، توجد شركة تحمل نفس رقم السجل ، الرجاء إدخال رقم سجل آخر ..."){
        toast.error("عذراً ، توجد شركة تحمل نفس رقم السجل ، الرجاء إدخال رقم سجل آخر")
      }else{
          toast.success("تم إنشاء حسابك بنجاح")
          localStorage.setItem("user_corporate", JSON.stringify({ ...data }));
          localStorage.setItem("number_partners", data.number_partners);
          setTimeout(() => navigate("/add-partners-ar") , 2000);
      }
    })
    .catch((err) => {
      console.error(err);
    });
}

  const displayPage = ()=>{
    if(page === 0){
      return <PersonalInfoAr formErrors={formErrors} page={page} setPage={setPage} data={data} setData={setData}/>
    }else if(page === 1){
      return <CompanyInfoAr num={num} setNum={setNum} data={data} setData={setData}/>
    }
  }
  const FormTitle = ["معلومات شخصية" , "معلومات الشركة"]
  return(
    <div className='registerCorporate'>
        <header>
          <Link to="/register-ar" className="back_ar">
              <span>رجوع</span>
              <img src={back} alt=""/>
          </Link>
        </header>
        <div><Toaster /></div>
        <section className='register1_ar'>
          <header>
            <span className='icon'><BsPersonPlus/></span>
            <span className='text'>مستخدم جديد / شركة</span>
          </header>
          <div className='form-container'>
            <div className='header'>
              <h2>{FormTitle[page]}</h2>
            </div>
            <div className='body'>
              {displayPage()}
            </div>
            <div className='footer'>
              <button
              className='next mt-3 ms-2 py-1 px-4'
              onClick={(currPage) => {
                if(page === FormTitle.length-1){
                  handleSubmit();
                }else{
                setPage((currPage) => currPage + 1)
                }
            }}>
                {page === FormTitle.length-1 ? "إرسال" : "التالي"}
            </button>
            <button
              className='next mt-3 ms-2 py-1 px-4'
              disabled={page === 0}
              onClick={(currPage) => {
                setPage((currPage) => currPage - 1);
              }}
              >السابق</button>
            </div>
        </div>
        </section>
        <div className='background2-ar'>
            <img src={corporate} alt=""/>
        </div>
    </div>
)
}


export default RegisterCorporateAr;